/* Overwrite the bootstrap defaults */
.custom-dropdown {
  position: relative;
  text-align: right;
}
.custom-dropdown .btn {
  background-color: #fff;
  border: 1px solid #d5d9d9;
  border-radius: 8px;
  box-sizing: border-box;
  color: #0f1111;
  cursor: pointer;
  display: inline-block;
  font-size: 13px;
  line-height: 29px;
  padding: 3px 10px 3px 11px;
  position: relative;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  width: 150px;
  height: 100%;
}
.custom-dropdown .dropdown-menu {
  text-align: left;
  width: 300px;
  background: white;
  border: 1px solid #ccc;
  border-radius: 4px;
  max-height: 15rem;
  padding: 15ps;
  overflow-y: auto;
  position: absolute;
  z-index: 1000;
  top: 100%;
  right: 0;
}
.custom-dropdown .dropdown-menu::-webkit-scrollbar {
  width: 5px;
}
.custom-dropdown::-webkit-scrollbar-track {
  background: #f1f1f1;
}
.custom-dropdown::-webkit-scrollbar-thumb {
  background: #808080;
}
.custom-dropdown .form-check {
  padding: 5px 10px;
}
.custom-dropdown .form-check-input {
  margin-right: 10px;
}
.custom-dropdown .form-check-label {
  margin: 0;
}

input[type=checkbox]:checked + label:before {
  width: 21px;
  letter-spacing: 10px;
  font-family: FontAwesome;
  display: inline-block;
}

@media (max-width: 768px) {
  .custom-dropdown {
    position: relative;
    text-align: left;
    margin-left: 0 !important;
  }
  .custom-dropdown .btn {
    margin-left: 0;
    width: 100%;
  }
}