/* Overwrite the bootstrap defaults */
.notification-container {
  position: relative;
  text-align: right;
}
.notification-container .notification-icon-container {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.notification-container .notification-icon-container .notification-icon {
  position: relative;
  cursor: pointer;
}
.notification-container .notification-icon-container .notification-badge {
  position: absolute;
  top: -6px;
  left: 10px;
  background-color: #dc3545;
  color: #fff;
  width: 0.9rem;
  height: 0.9rem;
  border-radius: 50%;
  font-size: 0.6rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
}
.notification-container .notification-dropdown {
  margin-top: 15px;
  position: absolute;
  top: 10px;
  width: 300px;
  right: 0px;
  z-index: 1001;
}