/* Overwrite the bootstrap defaults */
.oncoKBTable-container .header {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 1rem;
  margin-top: 1rem;
}
.oncoKBTable-container .header .table-header-left {
  color: #0968c3;
  font-size: 1.2rem;
  display: flex;
  align-items: flex-end;
  font-weight: bold !important;
  flex: 1;
}
.oncoKBTable-container .header .table-header-right {
  display: flex;
  align-items: flex-end;
  flex: 1;
}
.oncoKBTable-container .header .table-header-right .search-container,
.oncoKBTable-container .header .table-header-right .dropdown-container {
  flex: 1;
  margin-left: 1rem;
}
@media (max-width: 768px) {
  .oncoKBTable-container .header {
    flex-direction: column;
    align-items: flex-start;
  }
  .oncoKBTable-container .header .table-header-right {
    flex-direction: row;
    width: 100%;
  }
  .oncoKBTable-container .header .table-header-right .search-container,
  .oncoKBTable-container .header .table-header-right .dropdown-container {
    width: 100%;
    margin-left: 1rem;
    margin-top: 1rem;
  }
}
.oncoKBTable-container .text-center {
  text-align: center;
}
.oncoKBTable-container .justify-center {
  justify-content: center;
}
.oncoKBTable-container .fixedHeight {
  max-height: 400px;
  overflow-y: scroll;
}

.oncokbReactTable.ReactTable .-sort-desc {
  box-shadow: none !important;
}
.oncokbReactTable.ReactTable .-sort-desc:before {
  font-family: FontAwesome;
  content: "\f0dd";
  float: right;
}
.oncokbReactTable.ReactTable .-sort-asc {
  box-shadow: none !important;
}
.oncokbReactTable.ReactTable .-sort-asc:before {
  font-family: FontAwesome;
  content: "\f0de";
  float: right;
}
.oncokbReactTable.ReactTable .rt-table {
  border: none;
}
.oncokbReactTable.ReactTable .rt-table::-webkit-scrollbar {
  height: 0.5em;
}
.oncokbReactTable.ReactTable .rt-table::-webkit-scrollbar-track {
  background: #f1f1f1;
}
.oncokbReactTable.ReactTable .rt-table::-webkit-scrollbar-thumb {
  background: #d3d3d3;
}
.oncokbReactTable.ReactTable .rt-table .rt-thead {
  border: none;
}
.oncokbReactTable.ReactTable .rt-table .rt-thead .rt-resizable-header-content {
  border: none;
  font-weight: bold;
  justify-content: center;
  align-items: center;
  margin: 15px;
  white-space: normal;
  word-break: break-word;
}
.oncokbReactTable.ReactTable .rt-table .rt-tbody {
  border: none;
  overflow-x: hidden;
  overflow-y: hidden;
}
.oncokbReactTable.ReactTable .rt-table .rt-tbody::-webkit-scrollbar {
  width: 0.5rem;
}
.oncokbReactTable.ReactTable .rt-table .rt-tbody::-webkit-scrollbar-track {
  background: #f1f1f1;
}
.oncokbReactTable.ReactTable .rt-table .rt-tbody::-webkit-scrollbar-thumb {
  background: #d3d3d3;
}
.oncokbReactTable.ReactTable .rt-table .rt-tbody.has-data {
  overflow-x: hidden;
  overflow-y: auto;
}
.oncokbReactTable.ReactTable .rt-table .rt-tbody.no-data {
  overflow-x: hidden;
  overflow-y: hidden;
}
.oncokbReactTable.ReactTable .rt-table .rt-tbody .rt-td {
  border: none;
  text-align: center;
  white-space: normal;
  word-break: break-word;
  min-height: 7vh;
}

.no-results {
  min-height: 15vh;
  text-align: center;
  align-items: center;
  margin-top: 4vh;
}

.oncokbTable-main {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

