.alert {
  display: flex;
  align-items: center;
  padding: 1rem;
  border: 1px solid transparent;
  border-radius: 0.25rem;
  min-height: 3rem;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  background-color: white;
  text-align: left;
}
.alert .mr-2 {
  padding-right: 0.8rem;
}
.alert-danger {
  color: #721c24;
  background-color: #f8d7da;
  border-color: #f5c6cb;
}
.alert-primary {
  color: #004085;
  background-color: #cce5ff;
  border-color: #b8daff;
}
.alert-warning {
  color: #856404;
  background-color: #fff3cd;
  border-color: #ffeeba;
}
.alert-success {
  color: #155724;
  background-color: #d4edda;
  border-color: #c3e6cb;
}